.tests-grid {
  padding: 2rem;
  text-align: center;
}

.local-search {
  margin-bottom: 1.5rem;
  display: flex;
  justify-content: center;
  gap: 8px;
}

.local-search input {
  padding: 8px 12px;
  border-radius: 20px;
  border: 1px solid #ccc;
  width: 200px;
  transition: 0.2s;
}

.local-search input:focus {
  border-color: #ff80aa;
  box-shadow: 0 0 4px rgba(255, 128, 170, 0.5);
}

.local-search button {
  background: #ff80aa;
  color: white;
  border: none;
  border-radius: 20px;
  padding: 8px 14px;
  cursor: pointer;
}

.local-search button:hover {
  background: #ff4f8b;
}

.test-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 1rem;
}

.test-card {
  background: #fff9fc;
  border-radius: 12px;
  padding: 1.2rem;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.05);
  transition: all 0.2s ease;
  cursor: pointer;
}

.test-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 4px 12px rgba(255, 120, 160, 0.2);
}

.test-card h3 {
  font-size: 1.1rem;
  margin-bottom: 0.5rem;
  color: #ff5c93;
}

.test-card p {
  font-size: 0.9rem;
  color: #555;
  min-height: 40px;
}

.tags {
  margin-top: 0.6rem;
}

.tag {
  display: inline-block;
  margin: 2px;
  font-size: 0.75rem;
  color: #ff6a9a;
  background: rgba(255, 110, 150, 0.1);
  border-radius: 8px;
  padding: 2px 6px;
}

.no-result {
  margin-top: 2rem;
  color: #777;
}
